.registerContainer {
    padding-block: 2rem;
}

.registerForm {
    padding-inline: 1rem;
}

.registerInput {
    width: 100%;
    min-width: 280px;
}

.registerButton {
    width: 100%;
    min-width: 280px;
}

@media (min-width: 768px) {
    .registerContainer {
        padding-block: 5rem;
    }

    .registerForm {
        padding-inline: 10rem;
    }
}

@media (min-width: 1280px) {
    .registerForm {
        padding-inline: 20rem;
    }
}

@media (min-width: 1400px) {
    .registerForm {
        padding-inline: 25rem;
    }

}